'use strict';

import {isArray, isJson} from "../common/util";
import Pagination from "../Pagination";


// create pagination
const createPagination = _footer => {
	let {_root} = _footer._data;
	let {initData: {pagination}, _paginationWrap, _$cBWrap, layout} = _root._data;
	//
	let pageSize = layout.pageSize;
	// calculate page size
	let pageSizeOptions = {
		options: [pageSize, 2 * pageSize, 3 * pageSize, 4 * pageSize, 5 * pageSize],
		selected: 1
	};
	//
	let {pageNumber = 1, total} = _root.dataSource;
	// paginationInitData
	let paginationInitData = {
		// .,:1
		pageNumber,
		// .,
		total,
		// .,
		pageSize,
		// pageSizeChanger
		pageSizeOptions,
		//  pageSize. ,:true
		// showPageSizeChanger: false,
		// QuickJumper. ,:true
		// showQuickJumper: false,
		// . , Function(data) data={page, pageSize}
		onPageNumberChange: function (data) {
			// pageNumber:
			// pageSize:
			let {pageNumber, pageSize} = data,
				ajaxOps = _root._data.ajaxOps;
			//
			$.extend(ajaxOps.data, {pageNumber, pageSize});
			// ajax
			_root.ajax(ajaxOps);
		}
	}
	$.extend(paginationInitData, pagination, {pageNumber, total, el: _paginationWrap});
	// new pagination
	_root._data.paginationIns = new Pagination(paginationInitData);
}

export default createPagination;
