'use strict';


const defaultDelay = 250,// 
	maxDelaySeconds = 10;// dom

import {isDetached} from "./util";

// dom
const isMounted = async ($this, delay = defaultDelay) => {
	// console.log("mounted()");
	let currDelaySecond = 0,
		//
		intervalId;
	//
	return await new Promise((resolve, reject) => {
		//
		let mountedListener = () => {
			currDelaySecond += delay;
			// console.log("mountedListener()");
			// 
			if (currDelaySecond < maxDelaySeconds * 1000) { // dom
				if (!isDetached($this)) {
					// console.log("dom ");
					clearInterval(intervalId);
					resolve();
				}
			} else { // dom
				// console.log("dom");
				clearInterval(intervalId);
				reject();
			}
		}
		// setInterval
		intervalId = setInterval(() => {
			mountedListener();
		}, delay)
	})
}

export default isMounted;
