'use strict';

import {isJson, isString} from "../common/util";

// json a=1&b=2 ==> {a:1,b:2}
export const queryStringToJson = queryString => {
	if (isString(queryString)) {
		if (queryString.length === 0) return {};
		let args = {};
		let arr = queryString.split('&');
		arr.forEach(item => {
			let itemArr = item.split("="),
				decodeKey = itemArr[0],
				decodeVal = /%[0-9a-fA-F]{2,}/.test(itemArr[1]) ? decodeURIComponent(itemArr[1]) : itemArr[1];
			if (decodeKey) {
				args[decodeKey] = decodeVal;
			} else {
				console.error(`${queryString}`);
				throw new Error(`query stringjson${item}`);
			}
		});
		return args;
	}
	throw new Error(`${queryString}json!`);
}

// json {a:1,b:2} ==> a=1&b=2
export const jsonToQueryString = (json, encode = false) => {
	if (isJson(json)) {
		let arr = [];
		Object.keys(json).map(key => {
			// key
			if (/\W/.test(key)) {
				throw new Error(`json${key}key;`);
			}
			let val = encode ? encodeURIComponent(json[key]) : json[key];
			arr.push(`${key}=${val}`);
		})
		return arr.join("&");
	} else {
		let log = `jsonquery string;
----- string start -----
${json}
----- string  end -----`;
		throw new Error(log);
	}
}

