'use strict';

import {isUndefined, isJq, isArray, isFunction, isJson} from "../common/util";

// 
export default data => {
	let {columns, el, pagination, ajax, mounted, removed, pageSize} = data;
	//
	!isJq(el) && (data.el = $(el));
	// (Array)
	if (!isArray(columns)) {
		throw new TypeError("table! columns;  columns==" + columns);
	}
	// 
	if (!columns.length) {
		throw new TypeError("table! ; columns==" + columns);
	}
	// 
	let _$wrapper = $(el);
	if (!isJq(_$wrapper) || _$wrapper.length !== 1) {
		throw new TypeError("table!;  el==" + el);
	}
	// mounted
	if (mounted && !isFunction(mounted)) {
		throw new TypeError("table! mountedfunction; mounted==" + mounted);
	}
	// removed
	if (removed && !isFunction(removed)) {
		throw new TypeError("table! removedfunction; removed==" + removed);
	}
	// ajax
	if (ajax && !isJson(ajax)) {
		throw new TypeError("table! ajaxjson; ajax==" + ajax);
	}
	// return
	return true;
}
