'use strict';

import {isDetached} from "./util";
import isMounted from "./isMounted";

const resizeDelay = 250;// 

// resizeListener
const addResizeListener = ($this, cb, isMounted) => {
	let cw, ch, callbackArr = [], intervalId;
	// resizeListener
	const resizeListener = () => {
		let newW = $this.width();
		let newH = $this.height();
		if (newW !== cw || newH !== ch) {
			callbackArr.forEach(item => {
				cw = newW;
				ch = newH;
				if (cw !== 0 && ch !== 0) {
					item(cw, ch);
				} else {
					if ($this.closest("body").length === 1) {
						item(cw, ch);
					} else { // dom
						// console.log("dom remove");
						intervalId && clearInterval(intervalId);
					}
				}
			})
		}
	}

	// 
	if (!cw && !ch) {
		cw = $this.width();
		ch = $this.height();
		// isMounted
		isMounted && cb(cw, ch);
		//
		intervalId = setInterval(() => {
			resizeListener();
		}, resizeDelay)
	}
	callbackArr.push(cb);
}

// dom1
const onResize = ($this, cb) => {
	if (!isDetached($this)) { // dom,resizeListener
		addResizeListener($this, cb);
	} else { // dom,resizeListener
		isMounted($this).then(() => { // dom
			// let id = $this.attr("id");
			// console.log(`$("#${id}")`);
			addResizeListener($this, cb, true);
		}).catch(() => {
			$this.trigger("mountTimeout");
			// let id = $this.attr("id");
			// console.error(`v.onResize($("#${id}"))`);
		})
	}
}


export default onResize;
